import { createApp } from 'vue'; // Importar createApp de Vue 3
import App from './App.vue';
import router from './router';

// Variable global para almacenar ID del men pblico (si an la necesitas)
window.MENU_PUBLIC_ID = null;

// Funcin simple para detectar URLs pblicas
function detectPublicUrl() {
  try {
    const path = window.location.pathname;
    if (path.startsWith('/menu/')) {
      const menuId = path.split('/menu/')[1];
      if (menuId) {
        console.log('Detectada URL de men pblico:', menuId);
        window.MENU_PUBLIC_ID = menuId;
      }
    }
  } catch (error) {
    console.error('Error al detectar URL:', error);
  }
}

// Ejecutar deteccin
detectPublicUrl();

// Crear la instancia de la aplicacin Vue 3
const app = createApp(App);

// Usar el router
app.use(router);

// Montar la aplicacin en el elemento #app del HTML
app.mount('#app');